@media screen and ( max-width: 1400px ) {
    .container {
        max-width: calc(100% - 180px);
    }
}

@media screen and (max-width: 1200px) {
    .grid{
        gap: 20px !important;
    }

    .home__data{
        padding-right: 40px !important;
    }

    .home__title{
        font-size: var(--big-font-size) !important;
    }

    .home__description{
        font-size: var(--small-font-size) !important;
    }

    .stats__title{
        padding-left: 35px !important;
    }

    .stats__title::before{
        width: 22px !important;
    }

    .skills__container{
        row-gap: 32px !important;
    }

    .progress__circle{
        width: 100px !important;
        height: 100px !important;
    }

    .skills_title{
        margin-top: 20px !important;
    }

    .resume__icon {
        width: 36px !important;
        height: 36px !important;
        line-height: 38px !important;
    }

    .resume__item {
        padding-left: 55px !important;
    }

    .portfolio__modal-content {
        max-width: 600px !important;
    }

    .modal__close {
        width: 40px !important;
        height: 40px !important;
    }

    .form__input-group {
        grid-template-columns: repeat(2, 1fr) !important;
    }
    
    .form__input-group .form__input-div:last-child {
        grid-column: 1/-1 !important;
    }

    .form__input-div {
        margin-bottom: 24px !important;
    }
}

@media screen and (max-width: 1024px) {
    
    .container {
        max-width: 720px !important;
    }

    .home {
        padding: 20px !important;
        grid-template-columns: 5fr 7fr !important;
    }

    .home__data {
        padding-right: 0 !important;
    }

    .section{
        padding-block: 50px !important;
    }

    .section__title{
        margin-bottom: 40px !important;

    }

    .about__container,
    .resume__container,
    .contact__container{
        grid-template-columns: repeat(1, 1fr) !important;
    }

    .about__container{
        row-gap: 40px !important;
    }

    .subtitle__center {
        margin-bottom: 45px !important;
    }

    .skills__container {
        grid-template-columns: repeat(3, 1fr) !important;
    }

    .resume__container {
        row-gap: 45px !important;
    }

    .resume__item:not(:last-child) {
        margin-bottom: 45px !important;
    }

    .seperator {
        margin: 65px auto 50px !important;
    }

    .portfolio__container{
        grid-template-columns: repeat(2, 1fr) !important;
    }

    .contact__container{
        row-gap: 48px !important;
    } 
}

@media screen and (max-width: 768px) {
    .home{
        grid-template-columns: 1fr !important;
        justify-items: center !important;
        row-gap: 10px !important;
    }

    .color__block {
        display: none !important;
    }

    .home__img {
        width: 270px !important;
        height: 270px !important ;
        border-radius: 50% !important;
        margin-top: auto !important;
    }

    .home__content {
        max-width: 500px !important;
        text-align: center !important;
        margin-bottom: auto;
    }

    .home__title{
        padding-left: 0 !important;
    }

    .home__title::before {
        all: unset !important;
    }

    .home__title span {
        display: block !important;
    }

    .container{
        max-width: 540px !important;
    }

    .portfolio__modal-content {
        padding: 25px 20px !important;
    }

    .modal__close{
        top: -9% !important;
        right: 0 !important;
    }
}

@media screen and (max-width: 576px) {
    .container{
        /* padding-left: 4% !important; */
    }
    .nav__toggle{
        top: 20px !important;
        right: 15px !important;
    }

    .nav__link{
        column-gap: 20px !important;
    }

    .nav__icon,
    .nav__name{
        font-size: var(--h3-font-size) !important;
    }

    .skills__container{
        grid-template-columns: repeat(2, 1fr) !important;
    }

    .skills__container{
        grid-template-columns: repeat(2, 1fr) !important;
    }

    .portfolio__container {
        grid-template-columns: repeat(1, 1fr) !important;
    }

    .modal__list{
        grid-template-columns: repeat(1, 1fr) !important;
    }

    .modal__img {
        height: 300px !important;
    }

    .form__input-group {
        grid-template-columns: repeat(1, 1fr) !important;
    }
    .theme__img {
        width: 25px !important;
    }

    .style__switcher-toggler,
    .theme__toggler {
        width: 40px !important;
        height: 40px !important;
    }

    .style__switcher-toggler {
        top: 15%;
    }

    .theme__toggler{
        bottom: 15% !important;
    }

    .style__switcher-close {
        top: 5% !important;
    }
}

@media screen and (max-width: 450px) {
    .home__img {
        display: none !important;
    }

    .home__content {
        margin-block: auto !important;
        text-align: left !important;
    }

    .home__title {
        font-size: var(--h1-font-size) !important;
    }

    .home__description {
        text-align: justify !important;
    }

    .info__title {
        display: block !important;
        margin-bottom: 4px !important;
    }

    .stats__box {
        padding: 15px 15px 25px 20px !important;
    }
}

@media screen and (max-width: 350px) {
        .home__title span {
            display: inline !important;
        }

        .stats {
            grid-template-columns: repeat(1, 1fr) !important;
        }
}